import React, { useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import { useLanguage } from '../contexts/LanguageContext';
import ParticleBackground from '../components/ParticleBackground';
import { deleteUserAccount, loginUser, getCurrentUser, logoutUser } from '../services/supabase';

const DeleteAccount = () => {
  const { t, language } = useLanguage();
  const [isDeleting, setIsDeleting] = useState(false);
  const [isLoggingIn, setIsLoggingIn] = useState(false);
  const [showConfirmation, setShowConfirmation] = useState(false);
  const [confirmationText, setConfirmationText] = useState('');
  const [error, setError] = useState('');
  const [success, setSuccess] = useState(false);
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [user, setUser] = useState(null);
  const [loginForm, setLoginForm] = useState({
    email: '',
    password: ''
  });

  // Check if user is already logged in
  useEffect(() => {
    const checkUser = async () => {
      try {
        const currentUser = await getCurrentUser();
        if (currentUser) {
          setUser(currentUser);
          setIsLoggedIn(true);
        }
      } catch (error) {
        // User is not logged in
        setIsLoggedIn(false);
      }
    };
    checkUser();
  }, []);

  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        staggerChildren: 0.2,
        delayChildren: 0.3,
      },
    },
  };

  const itemVariants = {
    hidden: { opacity: 0, y: 50 },
    visible: {
      opacity: 1,
      y: 0,
      transition: {
        duration: 0.8,
        ease: "easeOut",
      },
    },
  };

  const handleLogin = async (e) => {
    e.preventDefault();
    setIsLoggingIn(true);
    setError('');

    try {
      const loginData = await loginUser(loginForm.email, loginForm.password);
      
      if (loginData && loginData.user) {
        setUser(loginData.user);
        setIsLoggedIn(true);
        
        // Force re-render by updating state again
        setTimeout(() => {
          setUser(loginData.user);
          setIsLoggedIn(true);
        }, 100);
      } else {
        throw new Error('Login data is invalid');
      }
    } catch (error) {
      console.error('Login error:', error);
      setError(error.message);
    } finally {
      setIsLoggingIn(false);
    }
  };

  const handleLogout = async () => {
    try {
      await logoutUser();
      setUser(null);
      setIsLoggedIn(false);
      setShowConfirmation(false);
      setConfirmationText('');
      setError('');
    } catch (error) {
      setError(error.message);
    }
  };

  const handleDeleteAccount = async () => {
    if (confirmationText !== 'DELETE') {
      setError(language === 'tr' ? 'Lütfen "DELETE" yazın' : 'Please type "DELETE"');
      return;
    }

    setIsDeleting(true);
    setError('');

    try {
      await deleteUserAccount();
      setSuccess(true);
      
      // Redirect to home page after 3 seconds
      setTimeout(() => {
        window.location.href = '/';
      }, 3000);

    } catch (error) {
      console.error('Delete account error:', error);
      setError(error.message);
    } finally {
      setIsDeleting(false);
    }
  };


  // Show success screen
  if (success) {
    return (
      <div className="min-h-screen bg-dark-bg text-white overflow-x-hidden">
        <ParticleBackground />
        
        <motion.div
          initial={{ opacity: 0, scale: 0.8 }}
          animate={{ opacity: 1, scale: 1 }}
          transition={{ duration: 0.8 }}
          className="relative z-10 min-h-screen flex items-center justify-center px-4"
        >
          <div className="max-w-md mx-auto text-center">
            <motion.div
              initial={{ scale: 0 }}
              animate={{ scale: 1 }}
              transition={{ delay: 0.3, type: "spring", stiffness: 200 }}
              className="w-20 h-20 bg-green-500 rounded-full flex items-center justify-center mx-auto mb-6"
            >
              <svg className="w-10 h-10 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 13l4 4L19 7" />
              </svg>
            </motion.div>
            
            <h1 className="text-3xl font-bold text-green-400 mb-4">
              {language === 'tr' ? 'Hesap Başarıyla Silindi' : 'Account Successfully Deleted'}
            </h1>
            
            <p className="text-gray-300 mb-6">
              {language === 'tr' 
                ? 'Hesabınız ve tüm verileriniz güvenli bir şekilde silindi. Ana sayfaya yönlendiriliyorsunuz...'
                : 'Your account and all data have been securely deleted. Redirecting to home page...'
              }
            </p>
            
            <div className="w-16 h-1 bg-gradient-to-r from-green-400 to-blue-400 mx-auto rounded-full" />
          </div>
        </motion.div>
      </div>
    );
  }

  // Debug: State kontrolü
  console.log('Render check:', { isLoggedIn, user: user?.email });

  // Show login form if not logged in
  if (!isLoggedIn) {
    console.log('Showing login form');
    return (
      <div className="min-h-screen bg-dark-bg text-white overflow-x-hidden">
        <ParticleBackground />
        
        {/* Header */}
        <motion.header
          initial={{ opacity: 0, y: -50 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8 }}
          className="relative z-10 py-8 px-4"
        >
          <div className="max-w-6xl mx-auto flex justify-between items-center">
            <motion.a
              href="/"
              whileHover={{ scale: 1.05 }}
              className="text-2xl font-bold gradient-text"
            >
              FEXIO LABS
            </motion.a>
            <motion.button
              whileHover={{ scale: 1.05 }}
              onClick={() => window.history.back()}
              className="px-6 py-2 bg-dark-surface border border-neon-cyan rounded-full text-neon-cyan font-semibold hover:bg-neon-cyan hover:text-dark-bg transition-all duration-300"
            >
              {t('privacyPolicy.backButton')}
            </motion.button>
          </div>
        </motion.header>

        {/* Login Form */}
        <motion.main
          initial={{ opacity: 0, y: 50 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8 }}
          className="relative z-10 py-20 px-4"
        >
          <div className="max-w-md mx-auto">
            <div className="text-center mb-8">
              <h1 className="text-4xl font-bold gradient-text mb-4">
                {language === 'tr' ? 'Giriş Yap' : 'Login'}
              </h1>
              <p className="text-gray-300">
                {language === 'tr' 
                  ? 'Hesabınızı silmek için önce giriş yapmanız gerekiyor'
                  : 'Please login to delete your account'
                }
              </p>
            </div>

            <div className="glass rounded-2xl p-8">
              <form onSubmit={handleLogin} className="space-y-6">
                <div>
                  <label className="block text-sm font-medium text-gray-300 mb-2">
                    {language === 'tr' ? 'E-posta' : 'Email'}
                  </label>
                  <input
                    type="email"
                    value={loginForm.email}
                    onChange={(e) => setLoginForm({...loginForm, email: e.target.value})}
                    required
                    className="w-full px-4 py-3 bg-dark-surface border border-gray-600 rounded-lg text-white focus:border-neon-cyan focus:outline-none"
                    placeholder={language === 'tr' ? 'ornek@email.com' : 'example@email.com'}
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-300 mb-2">
                    {language === 'tr' ? 'Şifre' : 'Password'}
                  </label>
                  <input
                    type="password"
                    value={loginForm.password}
                    onChange={(e) => setLoginForm({...loginForm, password: e.target.value})}
                    required
                    className="w-full px-4 py-3 bg-dark-surface border border-gray-600 rounded-lg text-white focus:border-neon-cyan focus:outline-none"
                    placeholder={language === 'tr' ? 'Şifrenizi girin' : 'Enter your password'}
                  />
                </div>

                {error && (
                  <div className="p-4 bg-red-900/20 border border-red-500 rounded-lg">
                    <p className="text-red-400">{error}</p>
                  </div>
                )}

                <button
                  type="submit"
                  disabled={isLoggingIn}
                  className="w-full px-6 py-3 bg-neon-cyan hover:bg-neon-blue disabled:bg-gray-600 disabled:cursor-not-allowed text-dark-bg font-bold rounded-lg transition-all duration-300"
                >
                  {isLoggingIn 
                    ? (language === 'tr' ? 'Giriş yapılıyor...' : 'Logging in...')
                    : (language === 'tr' ? 'Giriş Yap' : 'Login')
                  }
                </button>
              </form>
            </div>
          </div>
        </motion.main>
      </div>
    );
  }

  console.log('Rendering delete account page!');

  return (
    <div className="min-h-screen bg-dark-bg text-white overflow-x-hidden">
      <ParticleBackground />
      
      {/* Debug: Test content */}
      <div style={{position: 'fixed', top: '50px', left: '50px', background: 'red', color: 'white', padding: '20px', zIndex: 9999}}>
        DELETE ACCOUNT PAGE RENDERED!
      </div>
      
      {/* Header */}
      <motion.header
        initial={{ opacity: 0, y: -50 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.8 }}
        className="relative z-10 py-8 px-4"
      >
        <div className="max-w-6xl mx-auto flex justify-between items-center">
          <motion.a
            href="/"
            whileHover={{ scale: 1.05 }}
            className="text-2xl font-bold gradient-text"
          >
            FEXIO LABS
          </motion.a>
          <motion.button
            whileHover={{ scale: 1.05 }}
            onClick={() => window.history.back()}
            className="px-6 py-2 bg-dark-surface border border-neon-cyan rounded-full text-neon-cyan font-semibold hover:bg-neon-cyan hover:text-dark-bg transition-all duration-300"
          >
            {t('privacyPolicy.backButton')}
          </motion.button>
        </div>
      </motion.header>

      {/* Main Content */}
      <motion.main
        variants={containerVariants}
        initial="hidden"
        animate="visible"
        className="relative z-10 py-20 px-4"
      >
        <div className="max-w-2xl mx-auto">
          {/* User Info */}
          <motion.div variants={itemVariants} className="glass rounded-2xl p-6 mb-8">
            <div className="flex items-center justify-between">
              <div>
                <h3 className="text-lg font-semibold text-neon-cyan mb-1">
                  {language === 'tr' ? 'Giriş Yapıldı' : 'Logged In'}
                </h3>
                <p className="text-gray-300">{user?.email}</p>
              </div>
              <button
                onClick={handleLogout}
                className="px-4 py-2 bg-gray-600 hover:bg-gray-700 text-white rounded-lg transition-colors"
              >
                {language === 'tr' ? 'Çıkış Yap' : 'Logout'}
              </button>
            </div>
          </motion.div>

          {/* Title */}
          <motion.div variants={itemVariants} className="text-center mb-16">
            <h1 className="text-5xl md:text-6xl font-bold gradient-text mb-6">
              {language === 'tr' ? 'Hesabı Sil' : 'Delete Account'}
            </h1>
            <div className="w-32 h-1 bg-gradient-to-r from-red-500 to-orange-500 mx-auto rounded-full" />
          </motion.div>

          {/* Warning Content */}
          <motion.div variants={itemVariants} className="space-y-8">
            <div className="glass rounded-2xl p-8 border-l-4 border-red-500">
              <div className="flex items-start space-x-4">
                <div className="flex-shrink-0">
                  <svg className="w-8 h-8 text-red-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-2.5L13.732 4c-.77-.833-1.964-.833-2.732 0L3.732 16.5c-.77.833.192 2.5 1.732 2.5z" />
                  </svg>
                </div>
                <div>
                  <h2 className="text-2xl font-bold text-red-400 mb-4">
                    {language === 'tr' ? 'Dikkat: Bu İşlem Geri Alınamaz' : 'Warning: This Action Cannot Be Undone'}
                  </h2>
                  <p className="text-gray-300 leading-relaxed mb-4">
                    {language === 'tr' 
                      ? "Hesabınızı sildiğinizde, aşağıdaki tüm veriler kalıcı olarak silinecektir:"
                      : "When you delete your account, all of the following data will be permanently deleted:"
                    }
                  </p>
                  <ul className="text-gray-300 space-y-2 ml-6">
                    <li>• {language === 'tr' ? 'Kişisel bilgileriniz (ad, soyad, doğum tarihi)' : 'Your personal information (name, surname, date of birth)'}</li>
                    <li>• {language === 'tr' ? 'E-posta adresiniz ve hesap bilgileriniz' : 'Your email address and account information'}</li>
                    <li>• {language === 'tr' ? 'Uygulamalarınızdaki tüm veriler ve ayarlar' : 'All data and settings in your applications'}</li>
                    <li>• {language === 'tr' ? 'Satın alma geçmişiniz' : 'Your purchase history'}</li>
                    <li>• {language === 'tr' ? 'Profil bilgileriniz ve tercihleriniz' : 'Your profile information and preferences'}</li>
                  </ul>
                </div>
              </div>
            </div>

            {!showConfirmation ? (
              <motion.div variants={itemVariants} className="text-center space-y-6">
                <div className="glass rounded-2xl p-6">
                  <h3 className="text-xl font-bold text-orange-400 mb-4">
                    {language === 'tr' ? 'Hesap Silme İşlemi' : 'Account Deletion Process'}
                  </h3>
                  <p className="text-gray-300 mb-4">
                    {language === 'tr' 
                      ? 'Hesabınızı silmek için aşağıdaki butona tıklayın. Bu işlem geri alınamaz!'
                      : 'Click the button below to delete your account. This action cannot be undone!'
                    }
                  </p>
                </div>
                
                <button
                  onClick={() => setShowConfirmation(true)}
                  className="px-8 py-4 bg-red-600 hover:bg-red-700 text-white font-bold rounded-full transition-all duration-300 transform hover:scale-105 shadow-lg hover:shadow-red-500/25"
                >
                  {language === 'tr' ? 'Hesabımı Silmek İstiyorum' : 'I Want to Delete My Account'}
                </button>
              </motion.div>
            ) : (
              <motion.div 
                variants={itemVariants}
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                className="glass rounded-2xl p-8"
              >
                <h3 className="text-2xl font-bold text-orange-400 mb-6">
                  {language === 'tr' ? 'Son Onay' : 'Final Confirmation'}
                </h3>
                
                <p className="text-gray-300 mb-6">
                  {language === 'tr' 
                    ? 'Hesabınızı kalıcı olarak silmek istediğinizden emin misiniz? Bu işlem geri alınamaz.'
                    : 'Are you sure you want to permanently delete your account? This action cannot be undone.'
                  }
                </p>

                <div className="mb-6">
                  <label className="block text-sm font-medium text-gray-300 mb-2">
                    {language === 'tr' 
                      ? 'Onaylamak için "DELETE" yazın:'
                      : 'Type "DELETE" to confirm:'
                    }
                  </label>
                  <input
                    type="text"
                    value={confirmationText}
                    onChange={(e) => setConfirmationText(e.target.value)}
                    className="w-full px-4 py-3 bg-dark-surface border border-gray-600 rounded-lg text-white focus:border-red-500 focus:outline-none"
                    placeholder="DELETE"
                  />
                </div>

                {error && (
                  <div className="mb-4 p-4 bg-red-900/20 border border-red-500 rounded-lg">
                    <p className="text-red-400">{error}</p>
                  </div>
                )}

                <div className="flex space-x-4">
                  <button
                    onClick={handleDeleteAccount}
                    disabled={isDeleting || confirmationText !== 'DELETE'}
                    className="flex-1 px-6 py-3 bg-red-600 hover:bg-red-700 disabled:bg-gray-600 disabled:cursor-not-allowed text-white font-bold rounded-lg transition-all duration-300"
                  >
                    {isDeleting 
                      ? (language === 'tr' ? 'Siliniyor...' : 'Deleting...')
                      : (language === 'tr' ? 'Hesabı Kalıcı Olarak Sil' : 'Permanently Delete Account')
                    }
                  </button>
                  
                  <button
                    onClick={() => setShowConfirmation(false)}
                    disabled={isDeleting}
                    className="flex-1 px-6 py-3 bg-gray-600 hover:bg-gray-700 disabled:bg-gray-500 disabled:cursor-not-allowed text-white font-bold rounded-lg transition-all duration-300"
                  >
                    {language === 'tr' ? 'Geri Dön' : 'Go Back'}
                  </button>
                </div>
              </motion.div>
            )}

            {/* Help Section */}
            <motion.div variants={itemVariants} className="glass rounded-2xl p-8">
              <h3 className="text-xl font-bold text-neon-cyan mb-4">
                {language === 'tr' ? 'Yardıma mı İhtiyacınız Var?' : 'Need Help?'}
              </h3>
              <p className="text-gray-300 mb-4">
                {language === 'tr' 
                  ? 'Hesabınızı silmek yerine başka bir çözüm arıyorsanız, bizimle iletişime geçin:'
                  : 'If you\'re looking for an alternative to deleting your account, please contact us:'
                }
              </p>
              <div className="space-y-2">
                <p className="text-gray-300">
                  <span className="text-neon-cyan">{language === 'tr' ? 'Destek:' : 'Support:'}</span> 
                  <a href="mailto:support@fexiolabs.com" className="text-neon-cyan hover:text-neon-blue transition-colors ml-2">
                    support@fexiolabs.com
                  </a>
                </p>
                <p className="text-gray-300">
                  <span className="text-neon-cyan">{language === 'tr' ? 'Gizlilik:' : 'Privacy:'}</span> 
                  <a href="mailto:privacy@fexiolabs.com" className="text-neon-cyan hover:text-neon-blue transition-colors ml-2">
                    privacy@fexiolabs.com
                  </a>
                </p>
              </div>
            </motion.div>
          </motion.div>
        </div>
      </motion.main>
    </div>
  );
};

export default DeleteAccount;
